body, input, button, select {
  font-family: 'Karla', sans-serif;
  font-size: 16px;
}

html,
body,
.container {
  height: 100%;
}

*, *::before, *::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  line-height: 1.5em;
}

img {
  max-width: 100%;
}

input[type="text"] {
  width: 100%;
  border: 1px solid #efefef;
  height: 45px;
  padding: 5px;
  line-height: 35px;
}

.map {
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
}
.map img {
  max-width: 999999999px;
}
.map-overlay {
  position: absolute;
  z-index: 2;
  width: 300px;
  right: 20px;
  top: 50px;
  display: none;
  max-height: 90%;
  max-height: calc(100% - 100px);
  padding-top: 80px;
}
.map-overlay::before {
  content: "";
  display: block;
  position: absolute;
  top: -10px;
  left: 0;
  right: 0;
  background: #fff;
  height: 10px;
}
.map-overlay--show {
  display: flex;
}
.map-overlay__search {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}
.map-overlay__police-data {
  background: #fff;
  transform: scaleY(0);
  transform-origin: top;
  transition: transform 0.3s ease-in-out;
  width: 100%;
  overflow: auto;
}
.map-overlay__police-data__section {
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  transition-delay: 0.3s;
  position: relative;
  padding: 5px 0 5px 40px;
  cursor: pointer;
}
.map-overlay__police-data__section::before, .map-overlay__police-data__section::after {
  content: "";
  display: block;
  position: absolute;
  left: 10px;
  width: 20px;
  top: 5px;
  bottom: 5px;
}
.map-overlay__police-data__section::before {
  background: #efefef;
}
.map-overlay__police-data__section::after {
  background: red;
  opacity: 0;
}
.map-overlay__police-data__section--active::before {
  opacity: 0;
}
.map-overlay__police-data__section--active::after {
  opacity: 1;
}
.map-overlay__police-data--show {
  transform: scaleY(1);
}
.map-overlay__police-data--show .map-overlay__police-data__section {
  opacity: 1;
}
.map-overlay-toggle {
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 2;
  background: none;
  border: none;
}
.map-overlay-toggle svg {
  width: 20px;
}


